/**
 * LocalStorage
 */

export default class XStorage {

  static setValue(key, value) {
    localStorage.setItem(key, value);
  }

  static getValue(key) {
    return localStorage.getItem(key);
  }

  /**
   * key
   * @param key 
   */
  static popValue(key) {
    let value = localStorage.getItem(key);
    localStorage.removeItem(key);
    return value;
  }

  static removeValue(key) {
    if (typeof key === 'string') {
      localStorage.removeItem(key);
      return true;
    } else {
      return false;
    }
  }

  /**
   * 
   * @param key 
   * @param obj 
   */
  static setObjAsOne(key, obj) {
    if (typeof obj === 'object') {
      let objData = JSON.stringify(obj);
      localStorage.setItem(key, objData);
    } else {
      console.log('[setObjAsOne]');
    }

  }


  /**
   * Js
   * @param key 
   * @returns {null} 
   */
  static getObjAsOne(key) {
    //let objData = this.getObjectAsOne(key);
    let objData = localStorage.getItem(key);
    if (objData !== null) {
      return (JSON.parse(objData));
    } else {
      return null;
    }
  }

  /**
   * "+"
   * @param prefix 
   * @param obj   
   */
  static setObjSeparate(prefix, obj) {
    if (typeof obj === 'object') {
      (prefix.charAt(prefix.length - 1) !== '-') && (prefix = prefix + '-');
      for (var param in obj) {
        let key = prefix + param;
        if (typeof(obj[param]) !== 'function') {
          if (typeof(obj[param]) === 'object') {
            localStorage.setItem(key, JSON.stringify(obj[param]));
          } else {
            // aboutUs  aboutUs \r\n
            if (key == "common-config-aboutUs") {
              localStorage.setItem(key, JSON.stringify(obj[param]));
            } else {
              localStorage.setItem(key, obj[param]);
            }
          }
        } else {
          console.log(`${param},`);
        }
      }
    } else {
      console.log('[setObjSeparate]');
    }
  }

  /**
   * keyprefix
   * @param prefix 
   */
  static removeObjSeparate(prefix) {
    if (typeof prefix !== 'string') {
      return false;
    }
    for (let key in window.localStorage) {
      if (key.indexOf(prefix) === 0) {
        localStorage.removeItem(key);
      }
    }
    console.log('cache data in WebStorage with a prefix(', prefix, ') has been cleaned');
    return true;
  }

}


