
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
;

export default {
  name: 'index',
  components: {
    XHeader,
    Tabbar,
    TabbarItem
  },
  data () {
    return {
      transitionName: 'slide-left'
    }
  },
  computed: {
    title() {
      switch(this.currentRoute){
        case 'courtFormat':
          return '';
        default: 
          return '';
      }
    },
    isTabView() {
      // tab
      const tabMenus = ['tools', 'activites', 'user'];
      return tabMenus.indexOf(this.$route.name) >= 0;
    },
    currentRoute() {
      return this.$route.name;
    }
  },
  watch: {
    '$route' (to, from) {
      this.transitionName = calcTransition(to, from);
    }
  }
};

let _pathHistory = [];
//  slide-left     slide-right
function calcTransition(to, from) {
  let name;
  if (_pathHistory.length == 0 && from.path) {
    _pathHistory.push(from.path);
    name = 'slide-left';
  } else {
    const lastIndex = _.lastIndexOf(_pathHistory, to.path);
    if (lastIndex !== -1) {
      // AA 
      _pathHistory = _pathHistory.slice(0, lastIndex + 1);
      name = 'slide-right';
    } else {
      // 
      _pathHistory.push(to.path);
      name = 'slide-left';
    }
  }
  return name;
}



